.InputBackGround {
  background: #f8fcfd !important;
  border: 0 !important;
  height: 5vh !important;
}
.btn-blue-Color{
  /*background: linear-gradient(90deg, rgba(28,178,255,1) 1%, rgba(3,143,254,1) 100%);*/
  background: white!important;
border: 1px solid rgba(3,143,254,1) !important;
  color: rgba(3,143,254,1) !important;
  border-radius: 25px!important;
  font-weight: bold!important;
}
.btn-blue-Color:focus{
  box-shadow: 0 0 0 0.1rem rgba(3,143,254,1) !important;
}
.l-H-3{
  line-height: 3vh;
}
.pointer{
  cursor: pointer;
}
.redColor{
    color: red!important;
}
.padding-Zero{
  padding: 0!important;
}
.margin-Zero{
  margin: 0!important;
}

.bottomLanding{
  background-image: url("./Common/Img/bottom.png");

  /* Full height */
  height: 100%;

  /* Center and scale the image nicely */
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}


